<script>
    import cHeader from '@/components/header.vue';
    import cTop from './comps/top.vue';
    import Cnav from '@/components/nav.vue';
    import Ctrip from './comps/trip.vue';
    export default {
        components: {
            cTop,
            Ctrip,
            Cnav,
            cHeader
        },
        data() {
            return {
                
            };
        },
        created() {
            
        },
        mounted(){
          
        },
        methods: {
            loadMore() {
               
